<template>
  <div id="zhuce">
    <div class="box">
      <el-input label="用户名" type="string" v-model="name" placeholder="请输入用户名"></el-input>
      <el-input label="密码" type="string" v-model="password" placeholder="请输入密码" show-password></el-input>
      <div class="button">
        <el-button @click.native="zhuce">注册</el-button>
        <el-button @click.native="goback">返回</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      password: ""
    };
  },
  methods: {
    zhuce() {
      var data = {
        name: this.name,
        password: this.password
      };
      data = this.$Qs.stringify(data);
      this.$sendData("/register", data).then(res => {
        if (res.code == "01") {
          this.$message({
            message: "注册成功",
            type: "success"
          });
          this.$router.push('/login')
        } 
        if(res.code == "00") {
          this.$message({
            message: "注册失败",
            type: "warning"
          });
        }
        if(res.code == "06") {
          this.$message({
            message: "账户密码为空",
            type: "warning"
          })
        }
      });
    },
    goback() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="less">
#zhuce {
  padding-top: 200px;
  .box {
    width: 500px;
    height: 500px;
    margin: auto;
  }
  .button {
    width: 200px;
    margin: auto;
    margin-top: 20px;
  }
}
</style>